import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/home';
import Video from '@/components/video';
import Live from '@/components/live';
import Me from '@/components/me';


Vue.use(Router)

export default new Router({
  routes: [
  	{
  		path:"*",
  		redirect:"/home"
  	},
    {
      path: '/home',
      name: 'Home',
      component: Home
    },
    {
      path: '/video',
      name: 'Video',
      component: Video
    },
    {
      path: '/live',
      name: 'Live',
      component: Live
    },
    {
      path: '/me',
      name: 'Me',
      component: Me
    },
  ]
})



